/******************* Start footer  *******************/
footer {
  background: url(../images/dealing_bg.jpg);
  position: relative;
  text-align: center;
  padding: 80px 0;
  .overlay {
    background: linear-gradient(to right, #000000db, rgba(1,55,74,0.780392));
  }
  img {
    width: 110px;
    height: 110px;
    margin-bottom: 30px;
  }
  .about {
    color: #ccc;
    line-height: 1.8;
    font-size: 15px;
    width: 70%;
    margin: 0 auto;
  }
  .social {
    ul {
      display: flex;
      justify-content: center;
      margin: 30px 0;
      li {
        margin-right: 10px;
        background: #3F3E92;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        line-height: 40px;
        transition: all .3s ease;
        cursor: pointer;
        i {
          font-size: 20px;
          color: #fff;
          transition: all .3s ease;
        }
      }
      li:first-child {
        &:hover {
          background: #FFF;
          i {
            color: #3b5999;
          }
        }
      }
      li:nth-child(2) {
        &:hover {
          background: #FFF;
          i {
            color: #55acee;
          }
        }
      }
      li:last-child {
        &:hover {
          background: #FFF;
          i {
            color: #cd201f;
          }
        }
      }
    }
  }
  .copywright {
    color: #2857a6;
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  footer {
    .about {
      font-size: 13px;
      width: 100%;
      margin: 0;
    }
  }
}
/******************* End footer *******************/
