.header,.contactPage {
  overflow: hidden;
  background: url(../images/bg_1.png) no-repeat;
  background-size: cover;
  padding-top: 20px;
  height: auto;
  position: relative;
  .images {
    .astro {
      position: absolute;
      right: -9%;
      top: 29%;
      transform: rotateZ(-5deg);
      animation: astro 7s linear infinite alternate;
      z-index: 3;
      img {
        height: 394px;
      }
    }
    .earth {
      position: absolute;
      top: 114%;
      left: 35%;
      height: 6px;
      width: 900px;
      transform: rotateZ(40deg);
      animation: earth 19s linear infinite alternate;
      z-index: 3;
      img {
        position: absolute;
        height: 150px;
        width: 150px;
        top: 0;
        left: 0px;
      }
    }
  }
  .upper-head {
    .contact-box {
      border: 1px solid #403d5b;
      display: flex;
      width: 320px;
      border-right: 0;
      border-radius: 20px;
      margin-top: 30px;
      p {
        color: #FFF;
        flex-grow: 1;
        margin: 0;
        padding: 10px 0 10px 10px;
      }
      span {
        background: linear-gradient(to right,#2cc4d1c4 0%,#26489f 100%);
        color: #FFF;
        border-radius: 30px;
        flex-grow: 1;
        padding:10px;
        text-align: center;
        a {
          i {
            padding-left: 5px;
            font-size: 15px;
            color: #FFF;
            cursor: pointer;
            display: inline;
          }
        }
      }
    }
    .navbar {
      margin-top: 30px;
      position: relative;
      z-index: 999;
      .navbar-brand {
        font-size: 25px;
        padding-right: 8px;
        color: #0f43ff;
        border-radius: 10px;
        padding: 5px;
      }
      .navbar-toggler {
        outline: none;
        i {
          font-size: 30px;
          color: #EEE ;
        }
      }
      .navbar-collapse {
        .navbar-nav {
          position: relative;
          li.nav-item {
            margin-right: 12px;
            .nav-link {
              color: #cac7c7;
              cursor: pointer;
              transition: all .5s linear;
              &:hover {
                color: #FFF;
              }
            }
          }
          li.myDrop {
            .myDropLink {
              display: flex;
              span.text {
                padding-right: 7px;
              }
              span.icon {
                display: inline-block;
                background-color: #2f2c6c;
                border-radius: 50%;
                width: 15px;
                height: 15px;
                line-height: 11px;
                text-align: center;
                transition: all .2s linear;
                margin-top: 5px;
                i {
                  color: #FFF;
                  font-size: 10px;
                }
              }
            }
            .myDropMenu {
              background: linear-gradient(to right,#2cc4d1 0%,#26489f 100%);
              padding: 15px;
              position: absolute;
              left: 0;
              top: 40px;
              width: 100%;
              visibility: hidden;
              opacity: 0;
              transition: all .3s linear;
              li {
                padding: 10px;
                a {
                  color: #e3dbdb;
                  cursor: pointer;
                }
                &:hover {
                  a {
                    color: #FFF;
                  } 
                }
              }
            }
          }
        }
      }
    }
    .logo {
      padding: 30px 0 0 0;
      img {
        width: 76%;
        height: 100px;
        cursor: pointer;
      }
    }
  }
  .lower-head{
    margin: 120px 0 0 120px;
    width: 52vw;
    text-align: center;
    padding: 25px 15px;
    border-radius: 20px;
    h2 {
      font-size: 35px;
      color: #EEE;
    }
    p {
      color: #ccc;
      font-size: 18px;
      margin: 20px 0 30px 0;
    }
    button {
      color: #FFF;
      background: transparent;
      border: 1px solid #ccc;
      padding: 10px;
      border-radius: 10px;
      outline: none;
      font-weight: bold;
      transition: all .3s linear;
      &:hover {
        color: #00008f;
        background: #CCC;
      }
    }
  }
  .goDown {
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    bottom: 5px;
    width: 35px;
    height: 35px;
    background: linear-gradient(to right,#00047a,#FFF);
    line-height: 35px;
    text-align: center;
    z-index: 999;
    border-radius: 50%;
    cursor: pointer;
    transition: all .3s linear;
    &:hover {
      background: linear-gradient(to right,#fff,#00047a);
    }
    i {
      color: #FFF;
      font-size: 20px;
    }
  }
}

.col-nav {
  order: 2;
}
.col-logo {
  order: 1;
}

