@import "others/_variables.scss";
@import "others/_animations.scss";
@import "layouts/_header.scss";
@import "layouts/_footer.scss";
@import "pages/_contact.scss";
@import "pages/_glopal.scss";
/***************************/


/******************* Start About Us *******************/
.about-us {
  background: url("../images/about_bg.jpg");
  position: relative;
  padding: 80px 0;
  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000000e3;
    top: 0;
    left: 0;
  }
  .row {
    div:nth-child(3) {
      .item {
        .col-lg-8 {
          order: 2;
        }
        .col-lg-4 {
          order: 1;
        }
      }
    }
  }
  .upperInfo {
    padding-bottom: 125px;
    width: 70%;
    margin: 0 auto;
    h2 {
      color: #fff;
      font-size: 25px;
    }
    p {
      color: #ccc;
      font-size: 14px;
      margin-top: 11px;
    }
  }
  .item {
    margin-bottom: 50px;
    .image {
      padding-top: 35px;
      img {
        width: 100%;
      }
    }
    .info {
      h3 {
        color: #fff;
        font-size: 25px;
      }
      p {
        color: #ccc;
        font-size: 14px;
        margin-top: 11px;
      }
    }
  }
  .imagePos {
    width: 100%;
    height: 100%;
    img {
      position: absolute;
      height: 300px;
      top: 0;
      left: 10%;
      animation: astro2 15s linear infinite;
    }
  }
}

@media (max-width: 768px) {
  .about-us {
    .upperInfo {
      h2 {
        font-size: 20px;
      }
      p {
        font-size: 10px;
      }
    }
    .item {
      text-align: center;
      .info {
        h3 {
          font-size: 20px;
        }
        p {
          font-size: 10px;
        }
      }
    }
  }
}
/******************* End About Us *******************/

/******************* Start Dealing *******************/
.dealing {
  background: url(../images/dealing_bg.jpg);
  padding: 80px 0;
  position: relative;
  .overlay {
    background: #3418189e;
  }
  .upperInfo {
    padding-bottom: 80px;
    width: 70%;
    margin: 0 auto;
    h2 {
      color: #ccc;
      font-size: 25px;
    }
    p {
      font-size: 14px;
      margin-top: 11px;
      font-weight: bold;
    }
  }
  .images {
    width: 100%;
    position: relative;
    img:first-child {
      position: absolute;
      width: 100%;
      top: 0;
      left: 0;
    }
    img:last-child {
      position: absolute;
      width: 100%;
      top: 8px;
      left: 0;
      animation: spaceShip 10s linear infinite alternate;
    }
  }
  .items {
    .item {
      margin-bottom: 40px;
      .image {
        img {
          width: 100%;
        }
      }
      .info {
        h3 {
          font-size: 25px;
          color: #ccc;
        }
        p {
          font-size: 15px;
          font-weight: bold;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .dealing {
    .upperInfo {
      h2 {
        color: #ccc;
      }
      p {
        margin-top: 11px;
      }
    }
    .items {
      .item {
        text-align: center;
        .info {
          h3 {
            font-size: 20px;
          }
          p {
            font-size: 10px;
          }
        }
      }
    }
  }
}
/******************* End Dealing *******************/

/******************* Start portfolio *******************/
.portfolio {
  background: url(../images/dealing_bg.jpg);
  position: relative;
  padding: 80px 0 220px;
  color: #fff;
  .overlay {
    background: #04002ade;
  }
  h2.main {
    color: #fff;
    text-align: center;
    padding-bottom: 100px;
  }
  .navs {
    margin-bottom: 50px;
    text-align: center;
    button {
      background: transparent;
      border: 2px solid #ccc;
      width: 32px;
      color: #fff;
      border-radius: 8px;
      margin-right: 5px;
      transition: all 0.2s linear;
      outline: none;
      &:hover {
        background: orange;
      }
    }
  }
  .left {
    .owl-carousel-one {
      height: 100%;
      position: relative;
      margin-bottom: 50px;
      &:after {
        content: "";
        width: 100%;
        height: 23px;
        background: url(../images/screen-top_bg.png) no-repeat;
        background-size: contain;
        position: absolute;
        top: -22.5px;
        left: 0;
      }
      &:before {
        content: "";
        width: 100%;
        height: 113px;
        background: url(../images/screen-bottom_bg.png) no-repeat;
        background-size: contain;
        position: absolute;
        bottom: -100px;
        left: 0;
      }
      .owl-stage-outer {
        height: 100%;
        .owl-stage {
          height: 100%;
          .owl-item {
            height: 100%;
            img {
              height: 100%;
            }
          }
        }
      }
    }
  }
  .right {
    height: 100%;
    background: url(../images/astro-work.png) no-repeat;
    background-size: contain;
    background-position: right;
    .owl-carousel-tow {
      .owl-stage-outer {
        .owl-stage {
          .owl-item {
            width: 540px;
            height: 200px;
            div {
              width: 62%;
              margin-left: auto;
              height: 100%;
              padding-top: 15px;
              h2 {
                color: orange;
                font-size: 20px;
              }
              p {
                color: #000;
                font-size: 15;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 992px) {
  .portfolio {
    padding-bottom: 100px;
    .row {
      .col-lg-6:first-child {
        order: 2;
      }
      .col-lg-6 :last-child {
        order: 1;
      }
    }
    .navs {
      margin-bottom: 50px;
      .next {
        i {
        }
      }
      .prev {
        i {
        }
      }
    }
    .left {
      .owl-carousel-one {
        height: 50%;
        width: auto;
        margin: 19px auto;
        &:after {
          display: none;
        }
        &:before {
          display: none;
        }
      }
    }
    .right {
      background: none;
      .owl-carousel-tow {
        .owl-stage-outer {
          .owl-stage {
            .owl-item {
              height: auto;
              div {
                margin: 0;
                width: 100%;
                text-align: center;
                h2 {
                  font-size: 20px;
                }
                p {
                  font-size: 15;
                  color: #fff;
                }
              }
            }
          }
        }
      }
    }
  }
}
/******************* End portfolio *******************/

/******************* Start Products *******************/
.products {
  padding: 100px 0 100px 0;
  background: url(../images/dealing_bg.jpg);
  position: relative;
  .overlay {
    background: linear-gradient(to right, #865600ad, #ff0000a6);
  }
  .upperInfo {
    width: 70%;
    margin: 0 auto 188px;
    h2 {
      color: #000;
      font-size: 25px;
      margin-bottom: 28px;
    }
    p {
      color: #333;
      font-size: 18px;
      line-height: 1.8;
    }
  }
  .owl-carousel-three {
    height: 400px;
    .owl-stage-outer {
      height: 100%;
      .owl-stage {
        height: 100%;
        .owl-item {
          height: 100%;
          width: 60%;
          margin: 0 auto;
          img {
            width: 70%;
            height: 100%;
            margin: 0 auto;
          }
        }
      }
    }
    .owl-dots {
      display: flex;
      justify-content: center;
      position: absolute;
      width: 100%;
      top: -95px;
      .owl-dot {
        width: 300px;
        height: 50px;
        border: 1.5px solid #333;
        margin-right: 16px;
        border-radius: 20px;
        transition: all 0.2s linear;
        outline: none;
        font-size: 18px;
        color: #fff;
        background: linear-gradient(to right, #86003496, #002fff8f);
        &:hover {
          background: linear-gradient(to right, #002fff8f, #86003496);
        }
        span {
          display: inline-block;
          width: 100%;
          height: 100%;
          position: relative;
          &:after {
            content: "Hotel Reservation";
            top: 50%;
            left: 50%;
            position: absolute;
            transform: translate(-50%, -50%);
          }
        }
      }
      .owl-dot.active {
        background: linear-gradient(to right, #ffffff, #000000);
      }
      .owl-dot:first-child {
        span {
          &:after {
            content: "Restaurant";
          }
        }
      }
      .owl-dot:last-child {
        span {
          &:after {
            content: "Playing field";
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .products {
    .owl-carousel-three {
      height: 200px;
    }
    .owl-dot {
      font-size: 15px !important;
    }
    .upperInfo {
      width: 100% !important;
      h2 {
        font-size: 20px !important;
      }
      p {
        font-size: 12px !important;
      }
    }
  }
}
/******************* End Products *******************/

/******************* Start Clients *******************/
.clients {
  padding: 100px 0;
  background: url(../images/dealing_bg.jpg);
  position: relative;
  .overlay {
    background: linear-gradient(to right, #10026de3, #01374ac7);
  }
  .upperInfo {
    width: 70%;
    margin: 0 auto 150px;
    text-align: center;
    h2 {
      color: #fff;
      font-size: 25px;
      margin-bottom: 28px;
    }
    p {
      color: #ccc;
      font-size: 18px;
      line-height: 1.8;
    }
  }
  .leftImage {
    position: relative;
    img {
      position: absolute;
      display: block;
      top: -121px;
      right: -87px;
      z-index: 1;
    }
  }
  .ourClients {
    .client {
      height: 150px;
      background: #fff;
      position: relative;
      margin-bottom: 30px;
      border-radius: 20px;
      img {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}
@media (max-width: 768px) {
  .clients {
    .upperInfo {
      margin-bottom: 50px !important;
      width: 100% !important;
      h2 {
        font-size: 20px !important;
      }
      p {
        font-size: 12px !important;
      }
    }
  }
}
@media (max-width: 600px) {
  .products {
    .owl-carousel-three {
      .owl-dots {
        .owl-dot {
          font-size: 10px !important;
        }
      }
    }
  }
}
/******************* End Clients *******************/

/******************* Start contact *******************/
.contact {
  background: url(../images/dealing_bg.jpg);
  padding: 100px 0;
  position: relative;
  .overlay {
    background: linear-gradient(to right, #bcbbc4e3, #2c2e55c7);
  }
  .upperInfo {
    width: 70%;
    margin: 0 auto 120px;
    text-align: center;
    h2 {
      color: #000;
      font-size: 25px;
      margin-bottom: 28px;
    }
    p {
      color: #000;
      font-size: 18px;
      line-height: 1.8;
    }
    .buttons {
      margin: 50px 0 0;
      display: flex;
      justify-content: center;
      button {
        margin-right: 15px;
        border: 2px solid #333;
        background: transparent;
        color: #000;
        font-size: 18px;
        border-radius: 10px;
        display: flex;
        width: 190px;
        padding: 8px;
        align-items: center;
        align-content: center;
        &:hover {
          color: #007607;
          border-color: #037a00;
        }
        span {
        }
        img {
          width: 30px;
          height: 30px;
          margin-left: 17px;
        }
      }
      button.active {
        color: #007607;
        border-color: #037a00;
      }
    }
  }
  .row {
    justify-content: space-around;
    .item {
      text-align: center;
      margin-bottom: 60px;
      img {
      }
      p {
        margin-top: 30px;
        word-wrap: break-word;
      }
    }
  }
}
/******************* End contact *******************/

/******************* Start Email *******************/
.email {
  padding: 80px 0;
  background: url(../images/dealing_bg.jpg);
  position: relative;
  .overlay {
    background: linear-gradient(to right, #10026de3, #01374ac7);
  }
  form {
    display: flex;
    padding-top: 15px;
    input[type="submit"] {
      border-radius: 23px 0 0 23px;
      width: 100px;
      border: none;
      background: linear-gradient(to right, #10026de3, #01374ac7);
      color: #fff;
      font-weight: bold;
      &:hover {
        background: linear-gradient(to right, #01374ac7, #10026de3);
      }
    }
    input[type="email"] {
      flex-grow: 1;
      border-radius: 0 23px 23px 0;
      border: none;
      outline: none;
      padding: 10px;
    }
  }
  .info {
    p {
      font-size: 20px;
      color: #fff;
    }
    h2 {
      font-size: 30px;
      color: #fff;
    }
  }
}
@media (max-width: 992px) {
  .email {
    .info {
      text-align: center;
      margin-bottom: 20px;
      p {
        font-size: 30px;
      }
      h2 {
        font-size: 20px;
      }
    }
  }
}
/******************* End Email *******************/


