body {
  position: relative;
}
html {
}
* {
  box-sizing: border-box;
}
ul {
  padding-left: 0;
  list-style: none;
}
a {
  text-decoration: none !important;
}
.nicescroll-cursors {
  border: 1px solid #172e26 !important;
}

.overlay {
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.container {
  z-index: 2;
  position: relative;
}

.scrollToTop {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 35px;
  height: 35px;
  background: linear-gradient(to right, red, #fff);
  line-height: 35px;
  text-align: center;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s linear;
  &:hover {
    background: linear-gradient(to right, #fff, red);
  }
  i {
    color: #fff;
    font-size: 20px;
  }
}

/****** Start loading ******/
.loading-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #010515;
  z-index: 99999;
  .parent {
    margin: 180px auto;
    opacity: 0.8;
    width: 50px;
    height: 50px;
    .ball {
      background-color: #000;
      border: 5px solid rgba(0, 183, 229, 0.9);
      opacity: 0.9;
      border-top: 5px solid #000;
      border-left: 5px solid #000;
      border-radius: 50px;
      box-shadow: 0 0 35px #2187e7;
      width: 50px;
      height: 50px;
      margin: 0 auto;
      animation: orbit1 0.5s linear infinite;
    }
    .ball1 {
      background-color: #000;
      border: 5px solid rgba(0, 183, 229, 0.9);
      opacity: 0.9;
      border-top: 5px solid #000;
      border-left: 5px solid #000;
      border-radius: 50px;
      box-shadow: 0 0 15px #2187e7;
      width: 30px;
      height: 30px;
      margin-top: 10px;
      margin-left: 10px;
      position: relative;
      top: -50px;
      animation: orbit2 0.5s linear infinite;
    }
  }
}
/****** End loading ******/
