.contact-page {
  .contactPage {
    min-height: 300px ;
    overflow: visible !important;
  }
  .conc {
    padding: 80px 0;
    h2 {
      margin-bottom: 20px;
    }
    .image {
      position: relative;
      img {
        width: 74%;
        height: 365px;
        position: absolute;
        top: 50px;
        right: 100px;
      }
    }
    form {
      input,textarea {
        width: 100%;
        background-color: #EDECF3;
        color: #333;
        margin: 15px 0;
        border: none;
        border-radius: 15px;
        height: 50px;
        padding: 10px;
        outline: none;
      }
      textarea {
        height: 210px;
        resize: none;
      }
      input[type="submit"] {
        background: linear-gradient(to right, rgba(16,2,109,0.890196), rgba(1,55,74,0.780392));
        color: #FFF;
        &:hover {
          background: linear-gradient(to right, rgba(1,55,74,0.780392), rgba(16,2,109,0.890196));
        }
      }
      .number {
        position: relative;
        input {

        }
        select {
          position: absolute;
          width: 47px;
          height: 50px;
          top: 15px;
          right: 10px;
          border: none;
          background: #EDECF3;
          border-radius: 0 11px 11px 0;
          outline: none;
        }
      }
    }
  }
}
