@keyframes astro {
  from {
    right: -10%;
  }
  to {
    right: -3%;
  }
}
@keyframes astro2 {
  0% {
    top: 0;
    left: 10%;
  } 
  25% {
    top: 20%;
    left: 40%;
  }
  50% {
    top: 50%;
    left: 10%;  
  }
  75% {
    top: 20;
    left: 0%;
  }
  100% {
    top: 0;
    left: 10%; 
  }
}
@keyframes earth {
  100% {
    transform: rotateZ(161deg);
  }
}
@keyframes spaceShip {
  100% {
    top: 150px;
  }
}


@keyframes orbit1 {
  0% {
    transform: rotatez(0deg);
  }
  100% {
    transform: rotatez(360deg);
  }
}
@keyframes orbit2 {
  0% {
    transform: rotatez(0deg);
  }
  100% {
    transform: rotatez(-360deg);
  }
}